.video-mobile-seize {
  width: 100%;
  height: 100px;
}

.video-mobile {
  padding-bottom: 40px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.video-mobile-box {
  margin-top: 40px;
  width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.video-mobile-cover {
  margin-bottom: 20px;
  width: 300px;
  height: 200px;
  background-color: rgb(228, 227, 227);
}

.video-mobile-box > span {
  font-size: 28px;
  line-height: 36px;
  color: #333;
}
